<template>
  <div id="app">
    <!-- vueMVVMM-model,V-viewmodelviewviewmodel -->
    <!-- vuevueTODOList -->
       <h2>ToDoList</h2>   
      <input type="text" v-model="todo" placeholder="" @keydown="addDown($event)">
      <button @click="add($event)">+</button>
      <h2></h2>
      <br>
      <ul class="bing">
        <li v-for="(item,index) in list" :key="index" v-if="!item.checked">
          <input type="checkbox" v-model="item.checked" @change="saveList()"> {{index}}--{{item.title}}<button @click="del(index)" >-</button>
        </li>
      </ul>
      <br>
      <h2></h2>
      <br>
      <ul class="finish">
        <li v-for="(item,index) in list" :key="index" v-if="item.checked">
          <input type="checkbox" v-model="item.checked" @change="saveList()"> {{index}}--{{item.title}}<button @click="del(index)" >-</button>
        </li>
      </ul>
  </div>
</template>

<script>
import storage from './model/storage'
console.log(storage)
export default {
  name: 'app',
  data () {
    return {
      todo:'',
      list:[

      ]
      
    }
    
  },
  methods:{
    add(e){
      this.list.push({
          title:this.todo,
          checked:false
        });
        this.todo='';
       //H5API,json
       //localStorage.setItem('list',JSON.stringify(this.list));

       //
       storage.set('list',this.list)
    },
    addDown(e){
      if(e.keyCode==13){
          this.list.push({
          title:this.todo,
          checked:false
        });
        this.todo='';
        //H5API,json
       //localStorage.setItem('list',JSON.stringify(this.list));

        //
       storage.set('list',this.list)
      } 
    },
    del(key){
      // splice
       this.list.splice(key,1);
       //H5API,json
       //localStorage.setItem('list',JSON.stringify(this.list));

        //
       storage.set('list',this.list)
    },
    saveList(){
       //localStorage.setItem('list',JSON.stringify(this.list));

        //
       storage.set('list',this.list)
    }

  },
  mounted(){
    // vue vue
    //JsonJSon
    var list=storage.get('list');
    if(list){
      //list
      this.list=list;
    }
  }
}
</script>

<style lang="scss">
#app{
  h2{
    margin-left: 25%;
  }
  input{
    width:300px;
    height:30px;
    margin-left: 35%;
  }
  button{
    width: 100px;
    height: 30px;
    line-height: 30px;
  }
  .bing{
    
    ul{
      width: 500px;
    }
    li{
      display: block;
      width: 100%;
      height: 60px;
      line-height: 60px;
      margin: 0 auto;
      background: #eee;
      font-size: 24px;
      list-style-type: none;
    }
  }
  .finish{
    
    ul{
      width: 500px;
    }
    li{
      display: block;
      width: 100%;
      height: 60px;
      line-height: 60px;
      margin: 0 auto;
      background: skyblue;
      font-size: 24px;
      list-style: none;
    }
  }
}

</style>



